'use strict';

// MODULES //

import { assign as sincos } from '@stdlib/math-base-special-sincos';
import exp from '@stdlib/math-base-special-exp';
import Complex128 from '@stdlib/complex-float64';
import real from '@stdlib/complex-real';
import imag from '@stdlib/complex-imag';


// VARIABLES //

// Pre-allocate workspace array:
var WORKSPACE = [ 0.0, 0.0 ];


// MAIN //

/**
* Evaluates the cis function for a double-precision complex floating-point number.
*
* @param {Complex128} z - complex number
* @returns {Complex128} result
*
* @example
* import Complex128 from '@stdlib/complex-float64';
* import real from '@stdlib/complex-real';
* import imag from '@stdlib/complex-imag';
*
* var z = new Complex128( 0.0, 0.0 );
* // returns <Complex128>
*
* var out = ccis( z );
* // returns <Complex128>
*
* var re = real( out );
* // returns 1.0
*
* var im = imag( out );
* // returns 0.0
*
* @example
* import Complex128 from '@stdlib/complex-float64';
* import real from '@stdlib/complex-real';
* import imag from '@stdlib/complex-imag';
*
* var z = new Complex128( 1.0, 0.0 );
* // returns <Complex128>
*
* var out = ccis( z );
* // returns <Complex128>
*
* var re = real( out );
* // returns ~0.54
*
* var im = imag( out );
* // returns ~0.841
*/
function ccis( z ) {
	var re;
	var im;
	var e;

	re = real( z );
	im = imag( z );

	sincos( re, WORKSPACE, 1, 0 );
	if ( im !== 0.0 ) {
		e = exp( -im );
		WORKSPACE[ 0 ] *= e;
		WORKSPACE[ 1 ] *= e;
	}
	return new Complex128( WORKSPACE[ 1 ], WORKSPACE[ 0 ] );
}


// EXPORTS //

export default ccis;
